<template>
  <div>
    <el-container>
      <el-main>
        <el-row :gutter="0">
        
          <el-col :span="24" class="dpplayerdiv">
              <iframe allowfullscreen="true" allowtransparency="true" id="player" frameborder="0" 
              :src="caseUrl"></iframe>
          </el-col>
          <el-col class="gonn">
            <label for="select">切换解析地址:</label>
            <el-select v-model="selectUrlvlaue" placeholder="请选择" id="select">
              <el-option v-for="item in selectUrl" :key="item.id" :label="item.api_name" :value="item.api_url">
              </el-option>
            </el-select>
            
          </el-col>
          <el-col :span="24" class="moreAnniu">
            <div v-for="(item,index) in BofangList" :key="item.Url+index" class="anniu_Col">
              <router-link :to="`/vplayer?id=${vod.vod_id}&url=${item.Url}`" class="vod_a" target="_blank">
                 <el-button size="small">{{item.epi}}</el-button>
              </router-link>
            </div>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
    <el-container>
      <main>
        <el-col :span="24" class="jieshaodiv">
          <div class="dianYin">
            <el-col :span="6">
              <img class="minImg" :src="vod.vod_pic" />
            </el-col>
            <el-col :span="18">
              <div class="detail">
                <div class="title">{{ vod.vod_name }} ({{vod.vod_year}})</div>
                <div class="des">
                  类型:{{
                    vod.vod_area + "/" + vod.vod_class + "/" + vod.vod_lang + ""
                  }}
                </div>
                <div class="des">主演:{{ vod.vod_actor }}</div>
                <div class="des_content">
                  介绍:&nbsp;&nbsp;&nbsp;&nbsp;{{ vod.vod_content }}
                </div>
                <div class="desScore">
                  {{ vod.vod_score }}.0<span class="minScore">分</span>
                </div>
              </div>
            </el-col>
          </div>
          
        </el-col>
      </main>
      <el-aside width="280px" class="asideStyle">
        <TUijian :ListData="hotList"></TUijian>
      </el-aside>
    </el-container>
  </div>
</template>

<script>
import service from "../../utils/request";
import TUijian from "./Tuijian";
export default {
  name: "other",
  components: { TUijian },
  data() {
    return {
      vod: {
        vod_id: "",
        vod_name: "",
        vod_pic: "",
        vod_year: "",
        vod_class: "",
        vod_area: "",
        vod_lang: "",
        vod_score: "",
        vod_en: "",
        vod_blurb: "",
        vod_content: "",
        vod_remarks: "",
        vod_hits: "",
        vod_time: "",
        vod_director: "",
        vod_actor: "",
      },
      hotList: [],
      selectUrlvlaue:'https://okjx.cc/?url=',
      selectUrl:[],
      BofangList:[],
      BofangSrc:'',

    };
  },
  methods: {
    GsUrl(urls){
      // 处理 pgutv_all 格式播放
      let aRr2= urls.split(/[$#]/);
      let arrayUrl=[]
      for(let i=0;i<aRr2.length;i++){
        if(aRr2[i] && aRr2[i+1]){
          let vote = {};
          vote.epi = aRr2[i];
          vote.Url = aRr2[i+1];
          arrayUrl.push(vote);
         }
         i++; 
      }
      console.log(arrayUrl);
      this.BofangList=arrayUrl;
     
    },

  },
  computed:{
    caseUrl(){
      return this.selectUrlvlaue+this.BofangSrc ;
    }
  },
  mounted() {
    //设置播放地址
    this.BofangSrc=this.$route.query.url;

    // console.log(this.BofangSrc);
    // console.log(this.$route.query.id);
    // 当前电影  获取数据
    service.get("/api/id", {
        params: {
          id: this.$route.query.id,
        },
      })
      .then((result) => {
        this.vod = result.data[0];  //设置视频属性
        document.title = result.data[0].vod_name;
        this.GsUrl(this.vod.vod_play_url)
      })
      .catch((err) => {
        console.log(err);
      });
      // 获取解析接口
      service.get("/api/video").then((result) => {
        this.selectUrl = result.data;  //地址解析
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

<style scoped>
.el-container {
  width: 80%;
  margin: 0 auto;
}
.el-aside {
  margin-top: 20px;
}
.el-main {
  padding: 0px 10px 10px 0px;
  margin: 0;
}
#dplayer {
  margin-top: 15px;
  height: 580px;
  width: auto;
}
#player{
  height: 580px;
  width: 100%;
}
.dpplayerdiv {
  margin-top: 15px;
}
.dianYin {
  /* border: 1px solid var(--br); */
  margin-bottom: 10px;
}
.minImg {
  width: 164px;
  height: 220px;
}
.detail {
  padding: 5px 10px 5px 10px;
}
.title {
  font-size: 18px;
  margin-bottom: 5px;
  display: -webkit-box; /**对象作为伸缩盒子模型展示**/
  -webkit-box-orient: vertical; /**设置或检索伸缩盒子对象的子元素的排列方式**/
  -webkit-line-clamp: 1; /**显示的行数**/
  overflow: hidden; /**隐藏超出的内容**/
}
.title,
.title a {
  color: #333;
}
.des {
  padding: 2px;
  color: #333;
  font-size: 13px;
  margin-bottom: 5px;
  display: -webkit-box; /**对象作为伸缩盒子模型展示**/
  -webkit-box-orient: vertical; /**设置或检索伸缩盒子对象的子元素的排列方式**/
  -webkit-line-clamp: 1; /**显示的行数**/
  overflow: hidden; /**隐藏超出的内容**/
}
.des_content {
  height: 85px;
  padding: 1px 20px 1px 2px;
  color: #333;
  font-size: 13px;
  display: -webkit-box; /**对象作为伸缩盒子模型展示**/
  -webkit-box-orient: vertical; /**设置或检索伸缩盒子对象的子元素的排列方式**/
  -webkit-line-clamp: 5; /**显示的行数**/
  overflow: hidden; /**隐藏超出的内容**/
}
.jieshaodiv {
  margin-top: 20px;
  padding: 0;
}
.desScore {
  position: relative;
  top: 15px;
  font-size: 24px;
  color: rgb(255, 167, 38);
}
.minScore {
  font-size: 14px;
  padding: 0 0 0 3px;
}
.jieshao2 {
  margin-top: 20px;
}
.moreAnniu{
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.anniu_Col{
  margin-top: 10px;
  margin-right: 5px;
}
.gonn{
  margin-top: 15px;
}
</style>